@media (max-width: 999px) {
  .vertical-navbar {
    display: none;
  }

  .menu-icon {
    display: block;
  }

  .nav-links.show {
    display: flex;
  }

  
}

.user{
  background-color: var(--background);
  border-radius: 5px;
  padding:  5px;
  border: 2px solid var(--button);
  margin-bottom: 10px;
  display: flex;
}
.profile-text{
  font-size: 16px;
  color: var(--text);
  margin-right: 20px;
  letter-spacing: 1.5px;
  display: flex;
  gap: 5px;
  overflow: hidden;
} 

.to-hide-user{
  display:flex ;
  align-items: center;
  gap: 10px;
  margin-left: 9px;

}
